			if (opt.value) {
				value = opt.value;
			}

			if (value < 0) {
				value = 0;
			}
			
			if (value > maxValue) {
				value = maxValue;
			}

			// Determine pct
			valuePct = ((opt.value) / (maxValue)) * 100;

